import { FC, useEffect, useState } from "react";
import ambulatePage from "./ambulatePage.module.css"
import microphone from "../../assets/Microphone.png"
import microphoneDiactivated from "../../assets/MicrophoneDeactivated.png"
import sound_on from "../../assets/turn_on.mp3"
import sound_off from "../../assets/turn_off.mp3"
import { useNavigate, useParams } from "react-router";
import { Packer } from "docx";
import { setDoc } from "../../utils/docCreator";
import { useAppDispatch, useAppSelector } from "../../hooks/useTypedSelector";
import { setPatient } from "../../store/reducers/ambulateReducer";
import arrow from "../../assets/ArrowShift.png"
import { saveAs } from "file-saver";
import Modal from "../Modal/Modal";
import arrowWide from "../../assets/Arrow.png"
import arrowWideRotate from "../../assets/ArrowRotate.png"
import enter from "../../assets/Space.png"
import arrowRotateLeft from "../../assets/ArrowRotateLeft.png"

export const inputNames = [
    {name: "... ", placeholder: "  ", height: "60px"},
    {name: " ", placeholder: "19.09.1999", height: "60px"},
    {name: "", placeholder: "  ,   , ,  ,    ,   , ,  , ", height: "160px"},
    {name: " ", placeholder: "  ,   , ,  ,    ,   , ,  , ", height: "160px"},
    {name: " ", placeholder: " ,   ,   ", height: "100px"},
    {name: "", placeholder: "", height: "60px"},
    {name: "", placeholder: "180 ", height: "60px"},
    {name: "", placeholder: "100 ", height: "60px"},
    {name: "", placeholder: "56", height: "60px"},
    {name: " ", placeholder: "36,6 ", height: "60px"},
    {name: " ", placeholder: " -,  , , ,  ", height: "100px"},
    {name: "  ", placeholder: ",  , ", height: "60px"},
    {name: "", placeholder: "-, , ,  ", height: "100px"},
    {name: "- ", placeholder: " ", height: "60px"},
    {name: "  ", placeholder: " ", height: "60px"},
    {name: "- ", placeholder: " , . :  (, ),   ,   ", height: "150px"},
    {name: " ", placeholder: "120/70 ..", height: "60px"},
    {name: "", placeholder: "80  1 ", height: "60px"},
    {name: " ", placeholder: " , , ,   .   : 17  1 .    , , , , .  : ,  ,    .      , ,  , ,  ,  ,  , ,  .  , , , - - , , , , ,   , ,  , ,  ,  , ,  . ", height: "690px"},
    {name: " ", placeholder: "   ,  , , ,  .     / , ,      , , .    , .     ,  , , ,  , .  , , .   , .  2   /, , ,  , ,  ,     .", height: "530px"},
    {name: " ", placeholder: "   : ,  , ,   .  4-6   , , , , , , , ,  - ", height: "250px"},
    {name: "", placeholder: "  ( )", height: "60px"},
    {name: " ", placeholder: " , , , , , ,  ,   ", height: "100px"},
    {name: " ", placeholder: " :  15,  , .  :  1   .,   200  2   .,   2  2   .", height: "200px"},
]

const AmbulatePage: FC = () => {

    const dispatch = useAppDispatch()
    const { data } = useAppSelector(state => state.ambulate)

    const [value, setValue] = useState<any>({strings:[]})
    const [selectedField, setSelectedField] = useState(1)
    const [micActive, setMicActive] = useState(false)
    const [clear, setClear] = useState(false)
    const [modalActive, setModalActive] = useState(false)

    const turn_on = new Audio(sound_on)
    const turn_off = new Audio(sound_off)

    const navigate = useNavigate()
    const currentPath = window.location.pathname;
    const grammar = "#JSGF V1.0"
    const speechRecognitionList = new webkitSpeechGrammarList
    speechRecognitionList.addFromString(grammar, 1);
    
    window.SpeechRecognition = window.webkitSpeechRecognition
    const recognition = new SpeechRecognition()
    recognition.continuous = true
    recognition.lang = "ru"
    recognition.grammars = speechRecognitionList

    // eslint-disable-next-line
    const handlerKeyPress = (e: any) => {
        if (e.code === "Enter" && selectedField !== 0){
            e.preventDefault()
            if (micActive === true){
                turn_off.play()
                setMicActive(!micActive)
            }else{
                turn_on.play()
                setMicActive(!micActive)
            }
        }else if (e.code === "ArrowDown"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            let accessedElement = null
            if (selectedField < inputNames.length){
                accessedElement = document.querySelector(`[data-key="${selectedField + 1}"]`)
                setSelectedField(prevState => prevState + 1)
            }else{
                accessedElement = document.querySelector(`[data-key="${selectedField}"]`)
            }
            if (accessedElement != null){
                let headerOffset = 180;
                let elementPosition = accessedElement.getBoundingClientRect().top;
                let offsetPosition = elementPosition + window.pageYOffset - headerOffset;
            
                window.scrollTo({
                    top: offsetPosition,
                });
            }   
        }else if (e.code === "ArrowUp"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            let accessedElement = null
            if (selectedField === 1){
                accessedElement = document.querySelector(`[data-key="${selectedField}"]`)
            }else if (selectedField === 0){
                accessedElement = document.querySelector(`[data-key="${selectedField + 1}"]`)
                setSelectedField(prevState => prevState + 1)
            }
            else{
                accessedElement = document.querySelector(`[data-key="${selectedField - 1}"]`)
                setSelectedField(prevState => prevState - 1)
            }
            if (accessedElement != null){
                let headerOffset = 180;
                let elementPosition = accessedElement.getBoundingClientRect().top;
                let offsetPosition = elementPosition + window.pageYOffset - headerOffset;
            
                window.scrollTo({
                    top: offsetPosition,
                });
            }
        }else if(e.code === "ArrowLeft"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            navigate("/")
        }else if(e.code === "KeyP"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            Packer.toBlob(setDoc(data)).then(res => saveAs(res, "test.docx"))
        }else if(e.code === "ArrowRight"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            if (currentPath === "/tvoi_hod/ambulatePage"){
                setClear(true)
            }
        }else if(e.code === "ShiftRight"){
            e.preventDefault()
            if (micActive === true){
                return
            }
            setModalActive(!modalActive)
        }
    }

    useEffect(() => {
        if (clear === true){
            const newValue = value.strings.slice(0, value.strings.length)
            for (let i = 0; i < value.strings.length; i++){
                newValue[i] = ""
            }
            setValue({strings:newValue})
            dispatch(setPatient({data: newValue}))
        }
        setClear(false)
    },[clear])

    useEffect(() => {
        let currentArea:any = null
        let elem = null
        if (selectedField !== 0){
            elem = document.querySelector(`[parent-key="${selectedField}"]`)
            currentArea = elem?.querySelector('textarea')
        }
        if (currentArea != null){
            currentArea.style.height = inputNames[selectedField - 1].height
            currentArea.style.padding_top = '0px'
            currentArea.style.padding_bottom = '0px'
            currentArea.style.height = `${currentArea.scrollHeight}px`
        }
        // eslint-disable-next-line
    },[value])

    useEffect(() => {
        let element = document.querySelector(`[parent-key="${selectedField}"]`)
        let currentArea = element?.querySelector('textarea')
        if (selectedField !== 0){
            try{
                recognition.addEventListener('result', (e) => { 
                    const transcript = Array.from(e.results) 
                        .map(result => result[0]) 
                        .map(result => result.transcript) 
                        .join('') 
                        const newValue = value.strings.slice(0, value.strings.length)
                        if (value.strings[selectedField] !== "" && currentArea?.selectionStart && data[selectedField] !== undefined){
                            newValue[selectedField] = data[selectedField].slice(0,currentArea?.selectionStart + 1) + transcript.slice(0,1).toLowerCase() + transcript.slice(1,transcript.length)+ " " + data[selectedField].slice(currentArea?.selectionStart + 1, data[selectedField].length)
                            setValue({strings:newValue})
                            dispatch(setPatient({data: newValue}))
                        }
                        else if (value.strings[selectedField] !== "" && currentArea?.selectionStart && data[selectedField] === undefined){
                            newValue[selectedField] = transcript.slice(0,1).toUpperCase() + transcript.slice(1,transcript.length) + " "
                            setValue({strings:newValue})
                            dispatch(setPatient({data: newValue}))
                        }
                        else{
                            newValue[selectedField] = transcript.slice(0,1).toUpperCase() + transcript.slice(1,transcript.length) + " "
                            setValue({strings:newValue})
                            dispatch(setPatient({data: newValue}))
                        } 
                })
            }catch (e){

            }

            if (micActive === false){
                try{
                    recognition.start()
                }catch(e){}
                recognition.stop()
            }else{
                recognition.start()
            }
        }
        // eslint-disable-next-line
    },[micActive])    

    useEffect(() => {
        document.addEventListener("keydown", handlerKeyPress);
        return () => {
            document.removeEventListener("keydown", handlerKeyPress);
        }
    },[handlerKeyPress]);

    const handleEdit = (e:any) => {
        let element = document.querySelector(`[parent-key="${selectedField}"]`)
        let currentArea = element?.querySelector('textarea')
        if (currentArea?.selectionStart !== undefined){
            const newValue = value.strings.slice(0, value.strings.length)
            newValue[selectedField] = e.target.value
            setValue({strings:newValue})
            dispatch(setPatient({data: newValue}))
        } 
    }

    return(
        <div tabIndex={1} className={ambulatePage.body}>
            <div className={ambulatePage.header}>
                <p className={ambulatePage.state}>
                    :
                </p>
                {micActive
                ?
                <div className={ambulatePage.stateMic}>
                    <img src={microphone} alt="Not Found"/>
                    <p className={ambulatePage.micActive}>
                         
                    </p>
                </div>
                :
                <div className={ambulatePage.stateMic}>
                    <img src={microphoneDiactivated} alt="Not Found"/>
                    <p className={ambulatePage.micIgnore}>
                         
                    </p>
                </div>
                }
                <div className={ambulatePage.advice}>
                     &nbsp;<span style={{fontWeight:"bold"}}></span>&nbsp; &nbsp;<div className={ambulatePage.imgDiv}><img src={arrow} alt="Not Found"/>&nbsp;&nbsp;SHIFT</div>
                </div>
            </div>
            <div className={ambulatePage.section}>
                {inputNames.map((item, index) => 
                <div key={index} parent-key={index + 1} className={ambulatePage.section_item}>
                    <div>
                        <p data-key={index + 1} className={index + 1 === selectedField ? `${ambulatePage.section_item_field} ${ambulatePage.section_item_field_active}` : ambulatePage.section_item_field}>
                        {item.name}
                        </p>
                    </div>
                    <textarea placeholder={index + 1 === selectedField ? item.placeholder : ""} disabled={index + 1 === selectedField ? false : true} className={ambulatePage.section_item_textarea} style={{height: item.height, resize:"none"}} value={ data[index + 1] !== "" ? data[index + 1] : ""} onChange={(e) => handleEdit(e)}/>
                </div>
                )}
            </div>
            <Modal active={modalActive} setActive={setModalActive}>
                <div className={ambulatePage.modal}>
                    <div className={ambulatePage.advice_item}>
                         <span className={ambulatePage.advice_span}>&nbsp;  &nbsp;</span>  &nbsp;<div className={ambulatePage.imgDivArrow}><img src={arrowWide} alt="Not Found"/></div>&nbsp;&nbsp;<div className={ambulatePage.imgDivArrow}><img src={arrowWideRotate} alt="Not Found"/></div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                         <span className={ambulatePage.advice_span}>&nbsp;/&nbsp;</span>    &nbsp;<div className={ambulatePage.enterImgDiv}><img src={enter} alt="Not Found"/>&nbsp;&nbsp;ENTER</div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                         <span className={ambulatePage.advice_span}>&nbsp;&nbsp;</span>    &nbsp;<div className={ambulatePage.imgDivP}>P</div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                         <span className={ambulatePage.advice_span}>&nbsp;   &nbsp;</span>  &nbsp;<div className={ambulatePage.imgDivArrow}><img src={arrowRotateLeft} alt="Not Found"/></div>
                    </div>
                    <div className={ambulatePage.advice_item}>
                         <span className={ambulatePage.advice_span}>&nbsp; &nbsp;</span>  &nbsp;<div className={ambulatePage.shiftImgDiv}><img src={arrowWide} alt="Not Found"/>&nbsp;SHIFT</div>
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default AmbulatePage;